import { HeartFilled } from '@ant-design/icons'
import type { MenuProps } from 'antd'

type MenuItem = Required<MenuProps>['items'][number]
function getItem(
  label: React.ReactNode,
  key: React.Key,
  icon?: React.ReactNode,
  children?: MenuItem[],
  type?: 'group',
): MenuItem {
  return {
    key,
    icon,
    children,
    label,
    type,
  } as MenuItem
}

const items: MenuProps['items'] = [
  getItem('', 'leader-talks', <HeartFilled />, [
    getItem('', 'leader_one'),
  ]),
  getItem('', 'dailylifewithdian', <HeartFilled />, [
    getItem('', 'essay20231024'),
  ]),
]

